export default {
	data() {
		return {
			showBack: true,
			hotSearchList: [],
			historyList: [],
			keyWord: ''
		};
	},
	onLoad() {
		this.getHotSearchList();
		this.getHistorySearch();
	},
	methods: {
		historyClear() {
			let that = this;
			if(that.historyList.length==0){
				that.Tips({ title: "已经没有历史记录啦！"});
				return;
			}
			uni.showModal({
				title: '提示',
				content: '确定删除全部历史记录',
				success: function(res) {
					if (res.confirm) {
						that.historyList = []
						uni.clearStorage();
					} else if (res.cancel) {
						return;
					}
				}
			});
		},
		getHotSearchList() {
			var that = this;
			that.baseGet(
				that.U({ c: 'store_api', a: 'get_routine_hot_search' }),
				function(res) {
					that.hotSearchList = res.data;
				},
				function(res) {
					console.log(res);
				},
				true
			);
		},
		getHistorySearch() {
			let that = this;
			uni.getStorage({
				key: 'keyWord',
				success: res => {
					that.historyList = res.data.reverse();
				}
			});
		},
		//搜索并且添加历史搜索记录
		searchGoods() {
			let that = this;
			if (that.keyWord == '') {
				that.Tips({ title: '请输入关键字' });
				return;
			}
			//判断关键字是否存在历史记录之中
			if (that.historyList.indexOf(that.keyWord) == -1) {
				that.historyList.reverse();
				that.historyList.push(that.keyWord);
				uni.setStorage({
					key: 'keyWord',
					data: that.historyList
				});
				that.historyList.reverse();
			}
			//隐藏搜索键盘



			uni.navigateTo({
				url: '/pages/list/search_list/search_list?keyWord=' + this.keyWord
			});
		},
		// 点击关键字跳转
		toList(e) {
			uni.navigateTo({
				url: '/pages/list/search_list/search_list?keyWord='+e.currentTarget.dataset.key
			});
		},
		//返回上一页
		back() {
			uni.navigateBack();
		}
	}
};
